import React from "react"
import PasteDelegate from './PasteDelegate';

/**
 * 将微信公众号功能挪到这里来
 */
class Comp extends PasteDelegate {

  dealWith(text = '?') {
    fetch('https://ask.solutions/api/wx_web', {method: 'POST', body: text})
      .then(res => {
        const type = res.headers.get('Content-Type');
        if (type.includes('json')) {
          return res.json().then(json => JSON.stringify(json, null, 2))
        }
        return res.text();
      })
      .then(reply => {
        this.setState({text, reply});
      })
      .catch(err => {
        this.setState({text: text, reply: err.message});
      })

  }

  render() {
    const {text, reply} = this.state;
    return <main className="container">
      <div className="markdown-body">
        <pre><code style={{whiteSpace: 'pre-line'}}>{text}</code></pre>
        <br/>
        <pre style={{whiteSpace: 'pre-wrap'}}>
          {reply}
        </pre>
      </div>

    </main>
  }
}


export default Comp;
